@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@200;300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* ==== PHOTO CAROUSEL ==== */
.container-PhotoCarousel {
  margin: 0 5% 0 5%;
  background: linear-gradient(to right, #e3004c, #a17310, #47b200);
  box-shadow: rgb(73, 71, 71) 2px 2px 10px 1px;
  padding: 0.2%;
  border-radius: 6px;
  color: orangered;
  font-family: 'Roboto Slab', Arial, Helvetica, sans-serif;
  width: 90%;
  height: 50vw;
  overflow: hidden;
}

/* ==== IMAGE ==== */
.imageFeatured-PhotoCarousel {
  /* display: inline-block; */
  width: 100%;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}

